export default class getData {
  
  getAPIData(key) {
    var card_html = "";
    key = key == "" ? "adobe" : key;
    $.ajax({
      url: 'https://aravindtwitter.herokuapp.com/twittersearch?key=' + key,
      type: "GET",
      dataType: 'json',
      async: false,
      success: function (data) {
        $.each(data.statuses, function (index, value) {
          card_html +=
            `
            <div class="col-sm-6 col-12 col-md-12">
              <div class="card">
                <div class="row">
                <div class="col-md-2 col-sm-2 col-xs-12 img-alignment">
                  <img src="${value.user.profile_image_url}" class="img-responsive center-block">
                </div>
                <div class="col-md-10 col-sm-10 col-xs-12 align-vertical">
                  <div class="col-md-4 col-sm-4 col-xs-4 break-res" data-toggle="tooltip" data-placement="center" title="${value.user.name}">
                    ${value.user.name}
                  </div>
                  <div class="col-md-4 col-sm-4 col-xs-4 break-res" data-toggle="tooltip" data-placement="center" title="${value.user.screen_name}">
                    ${value.user.screen_name}
                  </div>
                  <div data-toggle="tooltip" data-placement="center" title="${value.created_at}" class="col-md-4 col-sm-4 col-xs-4 break-res">
                    ${value.created_at}
                  </div>
                  <div class="top col-md-12 col-xs-12 col-sm-12">
                    <b>${value.user.description}</b>
                  </div>
                </div>
              </div>
              </div>
              </div>
            `;
        });
      }
    });
    return card_html;
  }
}



