<template>
  <div class="twitter-content">
            <div class="container" v-if="totalcards" v-html="totalcards"></div>
            <div v-else="totalcards">No records Found</div>    
    <div class="container">
      <div id="footer">
        <span class="footer-block make-left">Copyrights by Twitter</span>
        <br>
        <span>
          <a class="make-right" target="_blank" href="https://twitter.com">Twitter.com</a>
        </span>
      </div>
    </div>
  </div>
</template>

<script>
import getData from "../services/getData.js";
export default {
  name: "twitter-content",
  components: {},
  props: {
    msg: {
      type: String
    }
  },
  data() {
    return {
      totalcards: "",
      APIObj: new getData(),
      busy: false,
      data: []
    };
  },
  mounted: function() {
    this.$root.$on("myEvent", text => {
      this.totalcards = text;
    });
  },
  methods: {
    refreshCards: function() {
      setInterval(() => {
        this.$root.$emit("countReset", true);
        this.totalcards = this.APIObj.getAPIData("adobe");
      }, 30000);
    }
  },
  created: function() {
    this.totalcards = this.APIObj.getAPIData("adobe");
    this.refreshCards();
  }
};
</script>

<style>
.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  width: 100%;
  min-height: 110px;
  border-radius: 5px;
  margin-top: 15px;
}

@media(max-width: 767px) {
    .card .img-responsive {
    background: #e0e0e0;
    width: 100%;
    height: 100px;
    padding: 13px;
    object-fit: contain;
    }
}
.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

.top {
  padding-top: 15px;
  height: 78px;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  overflow: hidden;
  text-align: left;
}

#footer {
  border-top: 2px solid #1da1f2;
  height: 60px;
  padding-top: 5px;
  margin-top: 50px;
}

.footer-block {
  margin: 20px 0;
}

.total-cards {
  max-width: 900px;
  margin: 0 auto;
}

.align-vertical {
  padding-top: 15px;
}

.break-res {
  text-overflow: ellipsis;
  overflow: hidden;
  text-align: left;
  height: 1.3em;
  white-space: nowrap;
  cursor: pointer;
}

.img-responsive {
  height: 60px;
  width: 60px;
  border-radius: 7px;
  object-fit: cover;
}
@media(min-width: 768px) {
.img-alignment {
  padding: 25px 5px;
}
}

@media(width: 768px) {
    .card .row {
        margin: 0;
    }
}
</style>




